<template>
  <div>
    <h1>视频列表</h1>
    <el-table :data="videoes">
      <el-table-column
        prop="_id"
        label="ID"
      />
      <el-table-column
        prop="category.name"
        label="所属分类"
      />
      <el-table-column
        prop="title"
        label="视频标题"
      />
      <el-table-column
        prop="url"
        label="链接"
      />
      <el-table-column
        prop="fre"
        label="点击率"
      />
      <el-table-column
        prop="date"
        label="发布日期"
      />
      <el-table-column
        prop="cover"
        label="封面"
      >
        <template slot-scope="scope">
          <img :src="scope.row.cover" style="height: 3rem;" alt="">
        </template>
      </el-table-column>
      <el-table-column
        fixed="right"
        label="操作"
        width="180"
      >
        <template slot-scope="scope">
          <el-button
            type="primary"
            size="small"
            @click="$router.push(`/videoes/edit/${scope.row._id}`)"
          >编辑</el-button>
          <el-button
            type="danger"
            size="small"
            @click="remove(scope.row)"
          >删除</el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  data() {
    return {
      videoes: []
    }
  },
  created() {
    this.fetch()
  },
  methods: {
    /* 获取视频数据 */
    async fetch() {
      const res = await this.$http.get('rest/videoes')
      this.videoes = res.data
    },
    /* 删除视频数据 */
    async remove(row) {
      this.$confirm(`是否确定要删除视频"${row.name}"?`, '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(async() => {
        await this.$http.delete(`rest/videoes/${row._id}`)
        this.$message({
          type: 'success',
          message: '删除成功!'
        })
        this.fetch() // 重新拉取数据
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        })
      })
    }
  }
}
</script>
